@import url(../molecules/article.css);

.welcome {
	color: var(--color-light-primary);
	overflow: hidden;
}

.welcome__body {
	position: relative;
}

.welcome__body:after {
	content: '';
	background-image: url(../../img/pc-decor.png);
	background-size: contain;
	background-repeat: no-repeat;
	position: absolute;
	z-index: -10;
}

@media (width < 728px) {
	.welcome__body:after {
		width: 17rem;
		height: 10rem;
		top: 0;
		right: -3.5rem;
	}
}

@media (728px <= width < 1024px) {
	.welcome__body:after {
		width: 30rem;
		height: 19rem;
		top: 0;
		right: 0;
	}
}

@media (1024px <= width < 1440px) {
	.welcome__body:after {
		width: 38rem;
		height: 24rem;
		top: -4rem;
		right: 2rem;
	}
}

@media (width >= 1440px) {
	.welcome__body:after {
		width: 53rem;
		height: 33rem;
		top: -4rem;
		right: -7rem;
	}
}

.welcome__article {
	padding-right: calc(100% - var(--welcome-article-width));
	position: relative;
}

@media (728px <= width < 1024px) {
	.welcome__article {
		--welcome-article-width: 29rem;
	}
}

@media (1024px <= width < 1440px) {
	.welcome__article {
		--welcome-article-width: 31rem;
	}
}

@media (width >= 1440px) {
	.welcome__article {
		--welcome-article-width: 37rem;
	}
}

.welcome__article:after {
	--welcome-article-after-height: 0.125rem;

	content: '';
	width: var(--welcome-article-after-width);
	height: var(--welcome-article-after-height);
	background-color: var(--color-light-primary);
	border-radius: var(--welcome-article-after-height);
	position: absolute;
	right: calc(50% - var(--welcome-article-after-width) / 2);
}

@media (width < 728px) {
	.welcome__article:after {
		--welcome-article-after-width: 9rem;
		bottom: -1rem;
	}
}

@media (728px <= width < 1024px) {
	.welcome__article:after {
		--welcome-article-after-width: 21rem;
		bottom: -10rem;
	}
}

@media (1024px <= width < 1440px) {
	.welcome__article:after {
		--welcome-article-after-width: 30rem;
		bottom: -10rem;
	}
}

@media (width >= 1440px) {
	.welcome__article:after {
		--welcome-article-after-width: 35rem;
		bottom: -19rem;
	}
}
